// Paleta de Colores
$negro: #393e46;
$green: #00af91;
$turquesa-claro: #aad8d3;
$blanco: #eeeeee;

.contact-container{
    background-color: $negro;
    color: $blanco;
    font-family: 'Nunito', sans-serif;
    h2{
        text-align: center;
        padding: 20px;
    }
    .icons-container{
        display: flex;
        justify-content: center;
        padding-bottom: 10px;
        a{
            color: $blanco;
        }
    }
}