// Paleta de Colores
$negro: #393e46;
$green: #00af91;
$turquesa-claro: #aad8d3;
$blanco: #eeeeee;



.welcome-section-container{
    background-color: $negro;
    color: $blanco;
    font-family: 'Nunito', sans-serif;
    height: 100vh;
    .img-container{
        padding-top: 80px;
        display: flex;
        justify-content: center;
    }
    .welcome-section{
        padding: 20px 20px 0px 20px;
        h1{
            padding: 30px 20px 0px 20px;
            font-weight: bold;
            letter-spacing: 2px;
        }
        p{
            font-weight: 600;
            padding: 10px 20px;
            font-size: 14px;
        }
    }
    .arrow-container{
        padding-top: 15px;
        padding-left: 20px;
        .arrow-down{
            width: 40px;
            height: 40px;
            margin: 0px 10px 10px 10px;
            color: $green;
        }
    }
    // Tablet y Desktop 
    
}

.welcome-section-container-secondary{
    padding-top: 150px;
    .welcome-section-secondary{
        padding-top: 50px;
        h1{
            font-size: 45px;
            font-weight: bold;
        }
        p{
            padding-top: 30px;
            font-size: 30px;
        }
    }
    .arrow-container-secondary{
        padding-top: 80px;
        .arrow-down-secondary{
            width: 80px;
            height: 80px;
            margin: 10px;
            color: $green;
        }
    }
}