// Paleta de Colores 
$negro: #393e46;
$green: #00af91;
$turquesa-claro: #aad8d3;
$blanco: #eeeeee;

.projects{
    font-family: 'Nunito', sans-serif;
    h2{
        padding: 20px;
        font-weight: bold;
    }
    p{
        font-weight: 600;
        font-size: 16px;
        padding: 0 20px;
    }
    .project-title{
        display: flex;
        justify-content: center;
        padding-bottom: 20px;
    }
    .project-title{
        color: $blanco;
        .tributePage-container{
            background-color: $negro;
            color: $blanco;
            .tributePage-container-body{
                display: flex;
                flex-direction: column;
            }
            .tributePage-link{
                background-color: $green;
                border: 1px solid $green;
                a{
                    text-decoration: none;
                    color:  $blanco;
                    font-weight: 600;
                }
            }
            
        }
    }

    .Form-container{
        display: flex;
        justify-content: center;
        padding: 20px 0;
        .Form-container-body{
            background-color: $negro;
            color: $blanco;
            display: flex;
            flex-direction: column;
        }
        .Form-link{
            background-color: $green;
            border: 1px solid $green;
            a{
                text-decoration: none;
                color: $blanco;
            }
        }
    }
}

// Version Tablet 

.projects{
    font-family: 'Nunito', sans-serif;
    padding: 30px 0;
    .card-body-container{
        background-color: $negro;
        color: $blanco;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

    }
    .card-footer{
        background-color: $negro;
        display: flex;
        justify-content: center;
        .card-link{
            background-color: $green;
            border: 1px solid $green;
            width: 100%;
            a{
                text-decoration: none;
                color: $blanco;
            }
        }
    }
}