// Paleta de Colores 
$negro: #393e46;
$green: #00af91;
$turquesa-claro: #aad8d3;
$blanco: #eeeeee;

// Navbar Mobile

.navbar-container{
    .desplegable-container{
        .logo-container{
            margin: 0 40px;
        }
        .boton-desplegable{
            background-color: $green;
            border: 1px solid  $green;
        }
    }
    .hv-container{
        width: 25%;
        text-align: center;
        border-radius: 10px;
        background-color: $green;
    }

}

// Navbar Desktop

#hv-cont{
    background-color: $green;
    width: 15%;
    text-align: center;
    border-radius: 10px;
}



